.TeamPage {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  .team-name-section {
    grid-column: span 4;
    justify-self: start;
    align-self: end;
  }
  .detail-section {
    grid-column: span 5;
  }
  .team-name {
    font-size: 4rem;
  }
  .percent-section {
    justify-self: center;
    title {
      font-size: small;
      color: white;
    }
  }
  .more-link {
    align-self: center;
    justify-self: center;
  }
}
.chart {
  text-align: center;
}
.piegraph {
  margin-top: 10%;
}
