@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}
a {
  color: lightgray;
}

.App{text-align:left;padding:20px}.win-card{background-color:#40923d;border:3px inset #40923d}.loss-card{background-color:#ec373f;border:3px inset #ec373f}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 4;justify-self:start;align-self:end}.TeamPage .detail-section{grid-column:span 5}.TeamPage .team-name{font-size:4rem}.TeamPage .percent-section{justify-self:center}.TeamPage .percent-section title{font-size:small;color:white}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center}.chart{text-align:center}.piegraph{margin-top:10%}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date{margin-top:10px}.MatchDetailCard .match-venue{margin-top:10px}.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:10px}

.MatchSmallCard{padding-top:5%;padding-left:5%}.MatchSmallCard p,.MatchSmallCard h4{padding-top:5%;padding-left:5%}

.MatchPage{display:grid;grid-template-columns:1fr 5fr}.MatchPage .yearSelector{justify-self:center;text-decoration:none;-webkit-align-self:start;align-self:start}.MatchPage .yearSelector .page-heading{justify-self:auto}.MatchPage .yearSelector ol{list-style-type:none;text-align:end}.MatchPage .yearSelector ol li{margin:10px auto}

.AllTeams{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr}.AllTeams .TeamName{margin:10%;border:5px outset gray}

