.MatchPage {
  display: grid;
  grid-template-columns: 1fr 5fr;
  .yearSelector {
    justify-self: center;
    text-decoration: none;
    align-self: start;
    .page-heading {
      justify-self: auto;
    }
    ol {
      list-style-type: none;
      text-align: end;
      li {
        margin: 10px auto;
      }
    }
  }
}
